.page__wrapper {
  min-height: 100%;
  margin: 0 auto;
  width: 300px;
  padding: 0 10px;
}

@media screen and (min-width: 768px) {
  .page__wrapper {
    width: 748px;
  }
}

@media screen and (min-width: 1140px) {
  .page__wrapper {
    width: 940px;
    padding: 0;
  }
}

* html .page__wrapper {
  height: 100%;
}

.page__buffer {
  height: 50px;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

h1, h2, h3 {
  margin: 0;
}

.footer {
  width: 280px;
  margin: 0 auto;
  text-align: center;
  padding: 22px 0;
}

.footer h3 {
  color: #FF4E50;
  font-weight: lighter;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 9px;
}

.footer__company-list {
  margin-bottom: 5px;
}

.footer__company-list,
.footer__partner-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 280px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer__company-list li:first-child a,
.footer__partner-list li:first-child a {
  font-weight: 400;
  line-height: 19px;
  color: #444;
  align-self: center;
}

.footer__company-list a,
.footer__partner-list a {
  padding: 0 5px;
  font-size: 14px;
  color: #999;
  line-height: 26px;
  font-weight: lighter;
  cursor: pointer;
}

.footer__logo {
  margin-top: 13px;
}

.footer__logo span {
  display: block;
  padding-bottom: 6px;
  font-size: 10px;
  line-height: 14px;
  color: #CCC;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .footer {
    width: auto;
    padding: 39px 40px 20px;
    display: flex;
    justify-content: space-between;
  }
  .footer__company-list {
    margin-bottom: 0;
  }
  .footer__company-list,
  .footer__partner-list {
    display: inline-block;
    width: 116px;
    text-align: left;
  }
  .footer__logo {
    margin-top: 0;
  }
  .footer__company-list a,
  .footer__partner-list a {
    padding: 0;
  }
  .footer__company-list li:first-child,
  .footer__partner-list li:first-child {
    margin-bottom: 10px;
  }
  .footer__logo span {
    text-align: right;
  }
}

@media screen and (min-width: 1140px) {
  .footer {
    padding-bottom: 36px;
  }
}

.page-header, .page-header__top-login,
.page-header__top-signin, .page-header__top-findbtn {
  font-size: 14px;
  line-height: 19px;
}

.page-header {
  padding-top: 20px;
  padding-bottom: 153px;
  color: #fff;
  background: url("../img/content/Background.jpg");
  -webkit-background-size: contain;
  background-size: cover;
  background-position: top center;
}

.page-header__top {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  font-weight: lighter;
}

.page-header__top-logo {
  font-size: 20px;
  line-height: 27px;
  text-transform: uppercase;
  margin-bottom: 23.5px;
}

.page-header__top-user {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 97px;
}

.page-header__top-login,
.page-header__top-signin {
