  }
  .page-header__top {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 40px;
    margin: 0 auto;
    margin-bottom: 153.5px;
  }
  .page-header__top-user,
  .page-header__top-logo {
    margin: 0;
  }
  .page-header__top-banner {
    width: auto;
  }
  .page-header__top-banner h1 {
    margin-bottom: 21.5px;
  }
  .page-header__top-findbtn {
    margin-top: 33px;
  }
