.interest {
  position: relative;
  width: 280px;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 40px;
}

.interest::after {
  position: absolute;
  bottom: 0;
  left: -1000px;
  content: '';
  overflow: hidden;
  width: 3500px;
  height: 2px;
  background: #FF4E50;
}

.interest h3 {
  padding-top: 35px;
  padding-bottom: 24px;
  color: #444;
  font-size: 22px;
  line-height: 26px;
  font-weight: lighter;
  text-align: center;
}

.interest b {
  display: block;
  padding-bottom: 14px;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: #666;
  font-weight: lighter;
}

.interest__form {
  margin: 0 auto;
}

.interest__form-input {
  width: 100%;
  color: #999;
  font-size: 14px;
  line-height: 19px;
  font-weight: lighter;
  padding: 10px 0;
  padding-left: 15px;
  margin-bottom: 10px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.interest__form-input[aria-placeholder="Enter your interest"] {
  color: #999;
  font-size: 14px;
  line-height: 19px;
  font-weight: lighter;
}

.interest__form-button {
  width: 100%;
  border-radius: 40px;
  color: #fff;
  background: #FF4E50;
  text-align: center;
  padding: 10px 0;
  font-size: 14px;
  line-height: 19px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-weight: lighter;
  border: none;
}

@media screen and (min-width: 768px) {
  .interest {
    width: 482px;
    padding-bottom: 100px;
  }
  .interest h3 {
    padding-top: 95px;
    padding-bottom: 43px;
  }
  .interest b {
    padding-bottom: 25px;
  }
  .interest__form {
    display: flex;
