  position: relative;
  width: 120px;
  height: 120px;
  margin: 0 auto;
  margin-bottom: 17px;
}

.meet-partner__avatar-block::after {
  position: absolute;
  right: 0;
  bottom: 0;
  content: "";
  width: 32px;
  height: 32px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}

.meet-partner__ava--1::after {
  background: url("../img/content/music2.png") no-repeat center, #FF4E50;
}

.meet-partner__ava--2::after {
  background: url("../img/content/camera.png") no-repeat center, #FFA710;
}

.meet-partner__ava--3::after {
  background: url("../img/content/brush.png") no-repeat center, #19D4CA;
}

.meet-partner__ava--4::after {
  background: url("../img/content/airplane.png") no-repeat center, #667DF3;
}

@media screen and (min-width: 768px) {
  .meet-partner {
    width: 492px;
    margin: 0 auto;
    padding-top: 95px;
  }
  .meet-pattner__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .meet-partner__item {
    width: 236px;
    height: 205px;
    margin-bottom: 35px;
    padding: 0;
  }
  .meet-partner h1 {
    width: 492px;
    line-height: 26px;
    margin-bottom: 35px;
  }
  .meet-partner__btn {
    width: 180px;
    text-align: center;
    padding: 10px 0;
  }
}

@media screen and (min-width: 1140px) {
  .meet-partner {
    width: auto;
  }
  .meet-pattner__content {
    flex-wrap: nowrap;
    margin-bottom: 35px;
  }
  .meet-partner__item {
    width: 221px;
    margin-bottom: 0;
  }
}
